<template>
  <div>
    <div class="outer_label">
      <img class="inner_label login_logo" src="../../assets/login/logo.png">
    </div>
    <div class="login_form">
      <input type="text"  class="qxs-ic_user qxs-icon"  placeholder="用户名" v-model="userName">
      <input type="text"  class="qxs-ic_password qxs-icon"  placeholder="密码" v-model="password">
      <!--<button class="login_btn el-button el-button&#45;&#45;primary is-round" type="primary" round>登录</button>-->
      <el-button class="login_btn" @click.native="login" type="primary" round :loading="isBtnLoading">登录</el-button>
      <div style="margin-top: 10px">
        <span style="color: #000099;" @click="login">注册</span><span style="float: right;color: #A9A9AB">忘记密码？</span>
      </div>
    </div>
  </div>
</template>

<script>
//  import { userLogin } from '../../api/api';
import JSEncrypt from 'jsencrypt'
import axios from 'axios'
import qs from 'qs'

export default {
  data () {
    return {
      userName: '',
      password: '',
      isBtnLoading: false
    }
  },
  created () {
    if (JSON.parse(localStorage.getItem('user')) && JSON.parse(localStorage.getItem('user')).userName) {
      this.userName = JSON.parse(localStorage.getItem('user')).userName
      this.password = JSON.parse(localStorage.getItem('user')).password
    }
  },
  computed: {
    btnText () {
      if (this.isBtnLoading) return '登录中...'
      return '登录'
    }
  },

  methods: {
    // 获得公钥，并加密密码
    async getPublicKey (user, pwd) { // 注册方法
      let para = {
        user: user
      }
      let rsapwd
      await axios.post(`http://localhost:8080/login/transfer`, qs.stringify(para))
        .then(res => {
          let encryptStr = new JSEncrypt()
          encryptStr.setPublicKey(res.data) // 设置 加密公钥
          let rsaPwd = encryptStr.encrypt(pwd.toString()) // 进行加密
          console.log('加密密码：' + rsaPwd)
          rsapwd = rsaPwd
        })
      return rsapwd
    },
    async add () { // 注册方法
      setTimeout(function () {
        return 100
      }, 1000)
      return 100
    },
    // 登录，传输加密密码
    login () {
      if (!this.userName) {
        this.$message.error('请输入用户名')
        return
      }
      if (!this.password) {
        this.$message.error('请输入密码')
      }
      if (this.userName && this.password) {
        let num = 0
        this.add().then(res => {
          num = res
          console.log('num:' + num)
        })
        this.getPublicKey(this.userName, this.password)
          .then(res => {
            console.log('rasPw' + res)
            let para = {
              loginName: this.userName,
              // passWord:this.password
              passWord: res
            }
            console.log('login')
            axios.post(`http://localhost:8080/login/authpwd`, qs.stringify(para))
              .then(res => {
                console.log('明文密码：' + res.data)
              })
          })

        // let rasPw = this.getPublicKey(this.userName, this.password) // ras 加密 密码
        // console.log('rasPw' + rasPw)
        // let para = {
        //   loginName: this.userName,
        //   // passWord:this.password
        //   passWord: rasPw
        // }
        // console.log('login')
        // axios.post(`http://localhost:8080/login/authpwd`, qs.stringify(para))
        //   .then(res => {
        //     console.log('明文密码：' + res.data)
        //   })
      }
    }
  }
}
</script>
<style>
  .login_form {
    padding-top: 10%;
    padding-left: 10%;
    padding-right: 10%;
  }
  .qxs-ic_user {
    background: url("../../assets/login/ic_user.png") no-repeat;
    background-size: 13px 15px;
    background-position: 3%;
  }
  .qxs-ic_password {
    background: url("../../assets/login/ic_password.png") no-repeat;
    background-size: 13px 15px;
    background-position: 3%;
    margin-bottom: 20px;
  }
  .login_logo {
    height: 70%;
  }
  .login_btn {
    width: 100%;
    font-size: 16px;
    background: -webkit-linear-gradient(left, #000099, #2154FA); /* Safari 5.1 - 6.0 */
    background: -o-linear-gradient(right, #000099, #2154FA); /* Opera 11.1 - 12.0 */
    background: -moz-linear-gradient(right, #000099, #2154FA); /* Firefox 3.6 - 15 */
    background: linear-gradient(to right, #000099 , #2154FA); /* 标准的语法 */
    filter: brightness(1.4);
  }
</style>



